
BODY{
  font-family: 'Helvetica Neue',sans-serif;
}

a{text-decoration: none;}
a:hover{text-decoration: none;}

.window {
  background: #fff;
  margin: auto;
  margin-top: 10px;
  border: 1px solid #acacac;
  border-radius: 6px;
  -webkit-box-shadow: 0px 0px 20px rgba(0,0,0,0.5);
          box-shadow: 0px 0px 20px rgba(0,0,0,0.5);
  position:absolute;
  z-index:0;
  overflow:hidden;
}

.titlebar {
  background: -webkit-gradient(linear, to left-top, to left-bottom, color-stop(0.0, #ebebeb, color-stop(1.0, #d5d5d5)));
  background: -webkit-linear-gradient(to top, #ebebeb, #d5d5d5);
  background: -o-linear-gradient(to top, #ebebeb, #d5d5d5);
  background: -webkit-gradient(linear, left bottom, left top, from(#ebebeb), to(#d5d5d5));
  background: -webkit-linear-gradient(bottom, #ebebeb, #d5d5d5);
  background: -o-linear-gradient(bottom, #ebebeb, #d5d5d5);
  background: linear-gradient(to top, #ebebeb, #d5d5d5);
  color: #4d494d;
  font-size: 11pt;
  line-height: 20px;
  text-align: center;
  width: 100%;
  height: 20px;
  border-top: 1px solid #f3f1f3;
  border-bottom: 1px solid #b1aeb1;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  position: fixed;
}

.titlebar p{
  display: inline-block;
  margin:0 0 0 -4em;
  padding:0;
  line-height: inherit;
}

.buttons {
  padding-left: 8px;
  padding-top: 3px;
  float: left;
  line-height: 0px;
}

.buttons:hover a {
  visibility: visible;
}

.close {
  background: #ff5c5c;
  font-size: 9pt;
  line-height:11px;
  width: 11px;
  height: 11px;
  border: 1px solid #e33e41;
  border-radius: 50%;
  display: inline-block;
}

.close:active {
  background: #c14645;
  border: 1px solid #b03537;
}

.close:active .closebutton {
  color: #4e0002;
}

.closebutton {
  color: #820005;
  visibility: hidden;
  cursor: default;
}

.minimize {
  background: #cccccc;
  font-size: 9pt;
  line-height: 11px;
  margin-left: 4px;
  width: 11px;
  height: 11px;
  border: 1px solid #aaaaaa;
  border-radius: 50%;
  display: inline-block;
}

.minimize:active {
  background: #cccccc;
  border: 1px solid #aaaaaa;
}

.minimize:active .minimizebutton {
  color: #5a2607;
}

.minimizebutton {
  color: #cccccc;
  visibility: hidden;
  cursor: default;
}

.zoom {
  background: #cccccc;
  font-size: 9pt;
  line-height: 11px;
  margin-left: 6px;
  width: 11px;
  height: 11px;
  border: 1px solid #aaaaaa;
  border-radius: 50%;
  display: inline-block;
}

.zoom:active {
  background: #029740;
  border: 1px solid #128435;
}

.zoom:active .zoombutton {
  color: #003107;
}

.zoombutton {
  color: #006519;
  visibility: hidden;
  cursor: default;
}

.content {
  padding: 10px;
  -webkit-box-sizing:content-box;
          box-sizing:content-box;
  overflow-y:scroll;
  overflow-x:hidden;
  height:100%;
  margin-right:-17px;
}



