@import url('https://fonts.googleapis.com/css?family=Cookie');

#btg_header{
  height:60px;
  width:100%;
  position: fixed;
  top:0;
  border-bottom:1px solid black;
  background-color: white;
  margin-top:-1.5vh

}

.red{
  color:red;
}

#btg_header h1{
  margin-top:2vh;
  font-size:20pt;
  font-family:'Cookie'
}

#btg_header h3{
  margin-top:-1.75em;
  font-size:9pt;
  font-family:Arial, Helvetica, sans-serif;
  font-weight: 100;
}

#iconMail{
  position:fixed;
  left:17px;
  top:11px;
  color:#666;
}

#iconMobile{
  position:fixed;
  top:11px;
  color:#666;
  right:22px;
}

#btg_stories{
  margin-top:35px;
  text-align: left;
  z-index:-1;
  padding-left:12px;
  padding-top:8vmax;
  padding-right:10px;
  border-bottom:1px solid black;
  background-color:#efefef;
}

#btg_stories.small{
  margin-top:-232px;
  opacity:0;
  }

#btg_stories ul{
  margin-top:-5px;
}

#btg_stories li{
    font-size:10px;
    margin-left:-26px;

}

.mobileFooter{
  background: #efefef;
  margin-top:-14px;
  height:40px;
}

.mobileFooter p{
  font-size:14px;
  padding-top:10px;
}

#btg_stories p{
  font-size:10px;
  margin-top:-13.8px;
}

#btg_headShot{
  width:40px;
  height:40px;
  border-radius:999px;
  border:1px solid grey;
  margin-top:-14px;
  float:left;
  margin-right:10px;
  vertical-align: top;
  margin-bottom:10px;
}

.sshow{
  background-color:#fff;
  border-bottom:1px solid black;
}

.sshow h4{
  margin:0.5em;
  text-align: left;
}

#videoFrame iframe{
  min-width:315px !important;
}



