@import url('https://fonts.googleapis.com/css?family=Cabin');

iframe{
  margin:0 auto;

}

#videoCounter{
  position: absolute;
  margin-left:auto;
  margin-right:auto;
  width:100%;
  bottom:0.5vmax;
  font-size:1em;
}

#videoTitle{
  margin: 1.5vmax;
  font-family: 'Cabin';
  font-size:2vmax;
}

#videoDescription p{
  text-align: left;
  padding:0 3em;
  font-size:0.85vmax;
}
#videoDescription a{text-decoration: underline;}
#arrowLeft{
  margin-top:-50%;
  margin-right:1em;
  vertical-align: middle;
  display: inline-block;
}
#arrowRight{
  margin-top:-50%;
  margin-left:1em;
  vertical-align: middle;
  display: inline-block;
}

#arrowLeft:hover,#arrowRight:hover{
  color:#bccbd8;
}

@media only screen and (max-width:1500px){
  #videoDescription p{
    font-size:12px;
  }
}

@media only screen and (max-width:1082px){

#videoTitle{
  font-size:20px;
  margin-bottom:10px;
}

#videoCounter{
  font-size:12px;
}

#videoDescription p{
  font-size:12px;
}

}



