@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700');

.MacFolder{
  position:absolute;
  top:0;
  left:0;
  text-align: center;
  padding:0.1em 0.1em 0 0.1em;
  border-radius: 10px;
  margin-bottom:0;
  z-index: 2;
  cursor:pointer;
  width:75px;
  height:72px;
  vertical-align: middle;
  overflow-x:visible;
}

.MacFolder p{
  width:118px;
  margin-left:-22px;
}

.highlighted{
background-color:rgba(224,224,224,0.3);

}
.MacFolder.highlighted p{
  background-color:#0069da;
  border-radius:11px;
  text-shadow:none;
}



.MacFolder img{
  width:75px;
  height:75px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}

.MacFolder p{
  font-family: 'Roboto', sans-serif;
  color:white;
  font-weight:500;
  text-shadow:2px 2px 4px #000;
  font-size:11pt;
  margin-top:-8px;
}



