@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,400,700');

#welcomeHolder.mobile{
  width:100%;
  overflow-x: hidden;
}


.welcome_top{
  text-align: left;
  margin-left:1em;
  height:5.5vmax;
  background-color:white;
  margin-left:-1em;
  margin-right:-1em;
  padding-top:1em;
  padding-left:2em
}


.welcome_bottom{
  margin-left:-1em;
  margin-right:-1em;
  color:white;
  padding:1em 2.2em 1em 2.2em;
  text-align: left;
}


.welcome_bottom h1{
  margin-bottom:-0.8em;
  margin-top:0em;
  font-size:1.1vmax;
}



.welcome_bottom p{
  line-height:2em;
  font-size:0.85vmax;
}

#bioTitle{
  font-size:3vmax;
  font-family: 'Source Sans Pro';
  margin-top:0em;
  min-width: 200px;
}

#bioImage{
  border-radius:500px;
  border:0.5vmax solid #6e7271;
  width:13vmax;
  height:13vmax;
  float:right;
  margin-top:1em;
  margin-right:1em;
  margin-left:0.5em;
}


#bioSubtitle{
  font-family: 'Source Sans Pro';
  font-weight: 400;
  font-size:1vmax;
  margin-top:-2em;
  margin-left:0.1em;
}

#bioSubtitle.mobile{
  position: inherit;
  font-size:2.5vmax;
  margin-top:-9vmax;
  margin-left:0.6vmax;
}

span.attn{
  font-size:1.1em;
  font-weight:bold;
}

  .welcome_bottom li{
    line-height:2em;
    font-size:0.8vmax;
  }

  .welcome_bottom ul{
    margin-top:-0.8vmax;
  }
@media only screen and (max-width:1400px){

  #bioTitle{
    font-size: 26pt;
  }

  #bioSubtitle{
    font-size: 15px;
  }

  #bioImage{
    width:140px;
    height:140px;
    border: 5px solid #6e7271;
  }

  .welcome_bottom p{
    font-size:12px;
  }

  .welcome_bottom h1{
    font-size:12px;
    margin-top:-5px;
  }

  .welcome_top{
    height:62px;
  }

  .email{
    font-size:13px;
  }

  .welcome_bottom li{
    font-size:12px;
  }

}



